<template>
  <div class="configuration">
    <div v-title>{{ title }}</div>
    <ul>
      <li>
        <span class="list-name">服务点名称</span>
        <span>星巴克店·专注中大校园的公益行</span>
      </li>
      <li>
        <span class="list-name">回收方式</span>
        <span class="text-orange status">线上</span>
        <span class="a-right">点击复制下单链接</span>
      </li>
    </ul>
    <p>回收收益操作</p>
    <ul>
      <li>
        <span class="list-name">用户自行操作</span>
      </li>
      <li>
        <span class="list-name">全额捐赠</span>
        <span class="a-right">点亮希望，让爱告白</span>
      </li>
      <li>
        <span class="list-name">限额捐赠</span>
        <span class="text-orange">15豆</span>
        <span class="a-right">点亮希望，让爱告白</span>
      </li>
    </ul>
    <p class="tip">仅为查看，不可编辑</p>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        title: '回收配置'
      }
    }
  }
</script>

<style lang="less" scoped>
  .tip {
    position: fixed;
    bottom: 0;
    left: 0;
    font-size: 12px;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .configuration {
    background: #F0F2F9;
    width: 100vw;
    height: 100vh;
    p {
      font-size: 14px;
      color: #9D9DA5;
      line-height: 14px;
      padding: 18px 15px;
    }
    ul {
      background: white;
      li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 18px 15px;
        font-size: 14px;
        color: #9D9DA5;
        line-height: 14px;
        border-bottom: 1px solid #E9ECF5;
        .list-name {
          font-size: 14px;
          color: #191D21;
          line-height: 14px;
        }
        .a-right {
          color: #66A7FF;
          text-decoration: underline;
        }
        .text-orange {
          color: #FFB717;
          &.status {
            background: rgba(255,183,23,0.08);
            border: 1px solid #FFB717;
            border-radius: 4px;
            padding: 5px 14.5px;
          }
        }
        &:last-child {
          border-bottom: none;
        }
      }
    }
  }
</style>



